body {
  font-family: "Prompt", sans-serif;
  padding: 16px;
  margin: 0;
  box-sizing: border-box;
}
.wrapper {
  list-style: none;

  display: flex;
  flex-direction: column;
  gap: 1em;
  align-items: center;
}

.wrapper li a {
  text-decoration: none;
}
h3 {
  text-align: center;
}

h3,
h4,
ul,
li,
img {
  list-style: none;
  margin: 0;
  padding: 0;
}

.wrapper {
  padding: 16px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.alert {
  padding: 15px;
  border: 1px solid #d6e9c6;
  border-radius: 4px;
  color: #3c763d;
  background-color: #dff0d8;
}

.main__list {
  padding: 10px;
}

.main__list li a {
  color: #7f7f7f;
  font-weight: 600;
  transition: all 0.5s;
}
.main__list li {
  padding: 4px;
}

.main__list li a:hover {
  color: #2196f3;
  transition: all 0.5s;
}

.page__title {
  padding: 4px 16px;
  border: 4px solid orange;
  color: #757575;
  border-radius: 20px;
}
#ref {
  color: #757575;
  font-weight: 600;
  text-decoration: none;
  /* border: 1px solid crimson; */
  border-radius: 15px;
  padding: 2px 25px;
  transition: all 0.5s;
}

.module-list > li > a {
  color: #757575;
  text-decoration: none;
  font-weight: 600;
  border-radius: 15px;
  padding: 2px 25px;
  transition: all 0.5s;
}

.module-list > li > a:hover,
.module-list > li > a:focus,
#ref:hover,
#ref:focus {
  outline: none;
  background-color: #2196f3;
  color: #dff0d8;
  transition: all 0.5s;
}
